import React from 'react';
import styles from './Users.module.css';
import Preloaded from "../Preolader/Preloader";
import Paginator from "../common/Paginator/Paginator";
import User from "./User";

const Users = ({totalUserCount, pageSize,currentPage,onPageChanged, isFetching, users, followProgress, unfollowUser, followUser, portionNumber, setPortionNumber}) => {
	return (
		<div className={"container bg " + styles.container__users}>
			{isFetching ? <Preloaded /> : ''}
			{users.map(u => <User
				user={u}
				followProgress={followProgress}
				unfollowUser={unfollowUser}
				followUser={followUser}
				key={u.id}
			/>)}
			<Paginator
				currentPage={currentPage}
				totalUserCount={totalUserCount}
				onPageChanged={onPageChanged}
				pageSize={pageSize}
				setPortionNumber={setPortionNumber}
				portionNumber={portionNumber}
			/>
		</div>
	)
}


export default React.memo(Users)