import React, {useState} from 'react';
import styles from './Paginator.module.css';


const Paginator = ({totalUserCount, pageSize, currentPage, onPageChanged, portionSize=10, setPortionNumber, portionNumber}) => {
	let pagesCount = Math.ceil(totalUserCount / pageSize);
	let pages = [];
	for (let i=1; i <= pagesCount; i++) {
		pages.push(i)
	}

	let portionCount = Math.ceil(pagesCount/portionSize);
	//локальный state не работает так как происходит двойной рендер, хз почему
	// let [portionNumber, setPortionNumber] = useState(1);
	let leftPortionPageNumber = (portionNumber-1) * portionSize + 1;
	let rightPortionPageNumber = portionNumber * portionSize;
	console.log(portionNumber)

	function nextPortion() {
		let portion = portionNumber + 1;
		setPortionNumber(portion);
	}
	function prevPortion() {
		let portion = portionNumber - 1;
		setPortionNumber(portion);
	}

	return <div>
		{portionNumber > 1 &&
			<button onClick={()=>prevPortion()}>PREV</button>}
		{pages
			.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
			.map(p => {
					return (<button
						className={currentPage === p
							? styles.selected_page
							: ''}
						key={p}
						onClick={(e) => {
							onPageChanged(p)
						}}
					>{p}</button>)
				})}
		{portionCount > portionNumber &&
			<button onClick={()=>nextPortion()}>NEXT</button>}
	</div>
}

export default React.memo(Paginator)